import React from 'react';
import './Deposit.css';

const $ = window.$;

function initNiceSelect () {
    $('select').niceSelect();
}

function awaitJquery () {
    console.log($);

    if ($) {
        initNiceSelect();

        return;
    }

    setTimeout(function () {
        try {
            awaitJquery();
        } catch (e) {
            throw new Error(e);
        }
    }, 500);
}


