import React, {Component, Fragment} from 'react';
import {Redirect, Route, Switch} from 'react-router-dom';
import {connect} from 'react-redux';
import AppLocale from '../lngProvider';
import {setInitUrl} from '../actions/Auth';
import {updateWindowWidth} from '../actions/Setting';
import "./styles.css";

import AccountApi from '../app/routes/AccountApi/AccountApi';
import AccountOverview from '../app/routes/AccountOverview/AccountOverview';
import Deposit from '../app/routes/Deposit/Deposit';
import Exchange from '../app/routes/Exchange/Exchange';
import Landing from '../app/routes/Landing/Landing';
import Settings from '../app/routes/Settings/Settings';
import SettingsPreferences from '../app/routes/SettingsPreferences/SettingsPreferences';
import SettingsSecurity from '../app/routes/SettingsSecurity/SettingsSecurity';
import Withdraw from '../app/routes/Withdraw/Withdraw';


const RestrictedRoute = ({component: Component, authUser, ...rest}) =>
  <Route
    {...rest}
    render={props =>
      authUser
        ? <Component {...props} />
        : <Redirect
          to={{
            pathname: '/landing',
            state: {from: props.location}
          }}
        />

      }
  />;

class App extends Component {

  componentDidMount() {
    window.__MUI_USE_NEXT_TYPOGRAPHY_VARIANTS__ = true;
    if (this.props.initURL === '') {
      this.props.setInitUrl(this.props.history.location.pathname);
    }
    this.props.updateWindowWidth(window.innerWidth);
  }
  componentDidUpdate =() => {
    this.props.updateWindowWidth(window.innerWidth);
  }


  render() {
    
    const {match, location, locale, authUser, initURL} = this.props;

    if (location.pathname === '/') {
      if (authUser === null) {
        return ( <Redirect to={'/landing'}/> );
      } else if (initURL === '' || initURL === '/' || initURL === '/signin' || initURL === '/signup' || initURL === '/terms') {
        return ( <Redirect to={'/signin'}/> );
      } else {
        var conf = initURL.split('/');
       if(conf[1] === 'confirm'){
        return ( <Redirect to={'/bxt/exchange'}/> );
       }else{
        return ( <Redirect to={initURL}/> );
       }
      }
    }

    const currentAppLocale = AppLocale[locale.locale];
    return (
      <Switch>
        <Route path='/account-api' component={AccountApi}/>
        <Route path='/account-overview' component={AccountOverview}/>
        <Route path='/deposit' component={Deposit}/>
        <Route path='/exchange' component={Exchange}/>
        <Route path='/landing' component={Landing}/>
        <Route path='/settings' component={Settings}/>
        <Route path='/settings-preferences' component={SettingsPreferences}/>
        <Route path='/settings-security' component={SettingsSecurity}/>
        <Route path='/withdraw' component={Withdraw}/>
        <Route path='' component={Landing}/>
      </Switch>
    );
  }
}

const mapStateToProps = ({settings, auth}) => {
  const {sideNavColor, locale, isDirectionRTL} = settings;
  const {authUser, initURL} = auth;
  return {sideNavColor, locale, isDirectionRTL, authUser, initURL}
};

export default connect(mapStateToProps, {setInitUrl, updateWindowWidth})(App);
